
.Subject {
    display: flex;
    flex: 1;
    flex-direction: row;
    /*padding: 5px;*/
    background-color: #293537;
    margin-top: 4px;
}

.Title {
    display: flex;
    justify-content: center;
    margin: 12px;
    font-weight: bold;
    color: #DFF4FB;
    min-width: 65px;
    align-items: center;
}
.SubjectTextButton {
    display: flex;
    flex: 1;
}
.SubjectText {
    display: flex;
    flex: 1;
    margin: 6px;
    color: #B5C6CC;
    text-decoration: none;
    justify-content: flex-start;
    align-items: center;
}

.EditContainer {
    display: flex;
    flex: 1;
    background-color: limegreen;
}
.EditForm {
    display: flex;
    flex: 1;
    flex-direction: row;
}
.TextInput {
    background-color: #0C1212;
    display: flex;
    flex: 1;
    font-size: 18px;
    color: #DFF4FB;
    border:none;
}

.SubmitButton {
    width: 1px;
    display: none;
}

.Seperator {
    width: 6px;
    background-color:#0C1212;
}

.Skrepaka {
    width: 25px;
    height: 23px;
    margin-top:11px;
    margin-right:6px;
}